.blog-detail {
  padding-top: rem(78);
  padding-bottom: rem(120);
}

.blog-detail__contents {
  margin-bottom: rem(56);
}

.blog__title {
  font-size: rem(20);
  line-height: rem(35);
  letter-spacing: 0.004em;
  margin-bottom: rem(16);
}

.blog__infomation {
  display: inline-block;
  width: 100%;
  margin-bottom: rem(16);
}

.blog__days {
  display: inline-block;
  line-height: rem(32.3);
  letter-spacing: 0.004em;
  margin-right: rem(24);
}

.blog__category {
  max-width: rem(128);
  width: 100%;
  text-align: center;
  border: 1px solid #ffffff;
  display: inline-block;

  a {
    display: inline-block;
    padding-top: rem(8);
    padding-bottom: rem(9);
    line-height: rem(23.2);
    letter-spacing: 0.02em;
  }
}

.blog__thumbnail {
  margin-right: calc(50% - 50vw);
  margin-left: calc(50% - 50vw);
  margin-bottom: rem(24);
  img {
    width: 100%;
    display: block;
    height: auto;
  }
}

.blog__text {
  line-height: rem(35);
  letter-spacing: 0.004em;
  text-align: left;
  margin-bottom: rem(24);
}

.blog__heading {
  font-size: rem(20);
  line-height: rem(29);
  letter-spacing: 0.02em;
  padding-left: rem(9);
  position: relative;
  margin-bottom: rem(24);

  &::before {
    content: "";
    width: rem(3);
    height: 100%;
    background: #c4c4c4;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.blog__subheading {
  font-size: rem(18);
  line-height: rem(26);
  letter-spacing: 0.02em;
  margin-bottom: rem(24);
  position: relative;
  padding-bottom: rem(3);
  display: inline-block;

  &::after {
    content: "";
    width: 100%;
    height: rem(3);
    position: absolute;
    bottom: 0;
    left: 0;
    background: #ffffff;
  }
}

.blog__lists {
  display: inline-block;
  margin-bottom: rem(24);
}

.blog__list {
  display: inline-block;
  line-height: rem(35);
  letter-spacing: rem(0.4);
}

.blog-detail__pagination {
  margin-bottom: rem(120);
  max-width: rem(309);
  width: 100%;
  margin: auto;
}

.blog-detail__relation {
}

.relation__title {
  max-width: rem(540);
  width: 100%;
  line-height: rem(23.2);
  letter-spacing: 0.02em;
  padding-top: rem(9);
  padding-bottom: rem(10);
  border: 1px solid #ffffff;
  text-align: center;
  margin-bottom: rem(32);
}

.relation__blogs {
}
